@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  color: #222;
}

body {
  background-color: #f8f8f8;
}

.App {
  margin: 1rem auto 0;
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.main-container {
  padding: 2.5rem 0;
  background-color: #eee;
  box-shadow: 0.1rem 0.1rem 0.5rem #999;
}

.component-container {
  padding: 2rem 4rem;
  display: flex;
  flex-direction: column;
}

.general-container {
  gap: 0.5rem;
}

.general-container p {
  font-size: 1.5rem;
}

.education-container,
.experience-container {
  gap: 2rem;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

.applicant-name {
  font-size: 3.5rem;
}

.education-heading,
.experience-heading {
  font-size: 2.5rem;
  border-bottom: #ccc 3px solid;
}

h3 {
  font-size: 2rem;
}

p,
label,
input {
  font-size: 1.25rem;
}

h1,
h2,
h3,
p,
label,
input {
  font-family: inherit;
  color: inherit;
}

.main-heading {
  text-align: center;
}

.component-container form,
.institution-container,
.company-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.edit {
  padding: 2rem;
  background-color: #f3f3f3;
  border: 1px solid #ccc;
  border-radius: 1rem;
  box-shadow: 1rem 1rem 2rem #777;
}

.top-row {
  display: flex;
  justify-content: flex-start;
  gap: 1.5rem;
  align-items: flex-end;
}

.top-row > button {
  opacity: 0;
  transition: all 250ms ease-in-out;
}

.top-row:hover > button {
  opacity: 1;
}

.form-input {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.form-input > label {
  width: 25%;
  text-align: end;
}

.form-input > input {
  /* flex: 1; */
  width: 60%;
  padding: 0.25rem;
  margin-right: 20%;
  border: none;
}

.button-container {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 1000px;
  color: #ddd;
  transition: all 100ms ease-in-out;
}

button:hover {
  transform: scale(1.05);
  color: #eee;
  box-shadow: 0.1rem 0.1rem 0.5rem #777;
  cursor: pointer;
}

.submit-btn {
  background-color: rgb(94, 142, 255);
  align-self: flex-start;
}

.remove-btn {
  background-color: rgb(189, 83, 26);
  align-self: flex-start;
}

.edit-btn {
  background-color: rgb(94, 142, 255);
}

.add-btn {
  background-color: rgb(11, 122, 11);
  margin-bottom: 0.3rem;
}

.general-container > .general-header {
  text-align: center;
}

.general-container > .top-row > .edit-btn {
  margin-bottom: 0.7rem;
}

.general-container > form > .submit-btn {
  margin: 0 auto;
}

.institution-container > h3,
.company-container > h3 {
  text-align: center;
}
